import { Dispatch, SetStateAction } from 'react';
import axios, { AxiosRequestConfig, AxiosResponse, AxiosError } from 'axios';
import { useProd, prodServer, server } from '../config.json';

import io from 'socket.io-client';

let errorCallback: (message: string) => void = () => {};

export const isDev = process.env.NODE_ENV === 'development'; //detects a react dev build

console.log('isDev', isDev);
const ping = !isDev || useProd ? prodServer : server;
export { ping as server };

axios.defaults.baseURL = ping;
export const resultsServer = '';

export function GET(options: AxiosRequestConfig): Promise<any> {
	options = Object.assign(options, {
		method: 'GET'
	});
	return axios(options)
		.then((res: AxiosResponse<any>) => res.data)
		.catch((e: AxiosError) => {
			console.error(e);
			if (e.response) errorCallback([e.response.status, e.response.statusText, ':', '/' + e.response.config.url?.split('/').pop()].join(' '));
			throw e;
		});
}

export function POST(options: AxiosRequestConfig): Promise<any> {
	options = Object.assign(options, {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json'
		}
	});
	return axios(options)
		.then((res: AxiosResponse<any>) => res.data)
		.catch((e) => {
			if (e && e.response && e.response.data) errorCallback(e.response.data);
			throw e;
		});
}

export function setErrorCallback(c: (message: string) => void) {
	errorCallback = c;
}

export const socket = io(ping);

export function connect(setConnected: Dispatch<SetStateAction<boolean>>): void {

	setConnected(socket.connected);

	socket.on('connect', () => {
		setConnected(true);
	});
	socket.on('connect_error', (e: Error) => {
		console.error(e);
		setConnected(false);
	});
	socket.on('connect_timeout', (e: Error) => {
		console.error(e);
		setConnected(false);
	});
}

export function emit(event: string, ...args: any[]): void {

    socket.emit(event, ...args);
}

